.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-userLayout {
  overflow: hidden;
  position: absolute;
  background-color: #e8e8e8;
  background-size: cover;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
